<template>
  <div id="base-container">
      <video ref="video" id="video-view" autoplay="true" playsinline></video>
      <canvas ref="canvas" id="video-canvas"></canvas>
      <div id="btn-group-container">
          <button id="btn-change-camera" class="btn-style" v-on:click="onChangeCamera"><img class="btn-group" :src="btn_change_camera" alt=""></button>
          <button id="btn-change" class="btn-style" v-on:click="onChangePhoto"><img class="btn-group" :src="btn_change_photo" alt=""></button>
          <button id="btn-shot" class="btn-style" v-on:click="onShotBtn"><img class="btn-group" :src="btn_shot" alt=""></button>
      </div>

      <div id="alert-popover" class="vertical-center">
        <div>
            <h4>AR虛擬教練</h4>
            <p>如無法開啟，請複製連結到手機的瀏覽器中開啟</p>
            <ul>
                <li>iOS請使用Safari開啟</li>
                <li>Android建議用Chrome或火狐開啟</li>
            </ul>
        </div>
        
        <div style="text-align: center;">
            <button id="button-start" class="btn btn-info" v-on:click="hideAlertView()">開始體驗</button>
            <button id="button-copy-link" class="btn btn-info" v-on:click="copyLink()">複製連結</button>
        </div>

        <div>
            <input id="url-input" type="text" :value="linkUrl">
        </div>
    </div>
  </div>
</template>

<script>
import CameraPhoto, {FACING_MODES} from 'jslib-html5-camera-photo';
import Hammer from 'hammerjs';
const cameraWidth = 1280;
const cameraHeight = 960;
const doctor_img_1 = require('@/assets/doctor_1.png');
const doctor_img_2 = require('@/assets/doctor_2.png');
const btn_change_photo = require('@/assets/btn_change_photo.png');
const btn_change_camera = require('@/assets/btn_change_camera.png');
const btn_shot = require('@/assets/btn_shot.png');
const maxImageScale = 1.5;
const minImageScale = 0.3;

export default {
    name: "Home",
    data: function() {
        return {
            videoEl: Element,
            canvasEl: Element,
            context: CanvasRenderingContext2D,
            image: doctor_img_1,
            imageSelectedId: 0,
            doctorImages: [doctor_img_1, doctor_img_2],
            btn_change_photo: btn_change_photo,
            btn_change_camera: btn_change_camera,
            btn_shot: btn_shot,
            cameraFacing: true, // true: 主鏡頭, false: 前鏡頭
            linkUrl: document.URL,
            imageInfo: {
                originScale: 0.5,
                scale: 0.5,
                originX: 0,
                originY: 0,
                x: 0,
                y: 0
            },
        }
    },
    computed: {
        doctorImage: function() {
            let img = new Image();
            let src = this.doctorImages[this.imageSelectedId];
            img.src = src;
            return img;
        }
    },
    mounted: function() {
        this.videoEl = this.$refs.video;
        this.canvasEl = this.$refs.canvas;
        this.context = this.canvasEl.getContext('2d');
        this.videoEl.control = false;
        this.cameraPhoto = new CameraPhoto(this.videoEl);
        this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {width: cameraWidth, height: cameraHeight}).then(() => {
            this.timerCallBack();
        }).catch(this.timerCallBack());
        // gesture event
        var manager = new Hammer(this.canvasEl);
        manager.on('pan', this.panGesture);
        manager.on('panend', this.panEndGesture);
        manager.get('pinch').set({ enable: true });
        manager.on('pinch', this.pinchGesture);
        manager.on('pinchend pinchcancel', this.pinchEndGesture);
        this.canvasEl.addEventListener('wheel', this.wheelEvent);
        window.addEventListener('resize', this.onScreenResize);
    },
    methods: {
        isPortrait: function() {
            return window.innerHeight > window.innerWidth;
        },
        getScreenWidth: function() {
            return window.innerWidth;
        },
        getScreenHeight: function() {
            return window.innerHeight;
        },
        onShotBtn: function() {
            this.openImageToNewTab();
        },
        onChangePhoto: function() {
            if (this.imageSelectedId == 0) {
                this.imageSelectedId = 1;
            } else {
                this.imageSelectedId = 0;
            }
        },
        onChangeCamera: function() {
            this.cameraFacing = !this.cameraFacing;
            if (this.cameraFacing) {
                this.cameraPhoto.startCamera(FACING_MODES.ENVIRONMENT, {width: cameraWidth, height: cameraHeight});
            } else {
                this.cameraPhoto.startCamera(FACING_MODES.USER, {width: cameraWidth, height: cameraHeight});
            }
        },
        startCountdown: function() {
            setInterval(() => {
                this.computeFrame();
            }, 100);
        },
        getCameraPhoto: function() {
            let dataUri = this.canvasEl.toDataURL();
            return dataUri;
        },
        imageSize: function() {
            return {
                width: this.doctorImage.width * this.imageInfo.scale, 
                height: this.doctorImage.height * this.imageInfo.scale
            };
        },
        imageToDataUri: function(image, width, height) {
            var canvas = document.createElement('canvas'),
            ctx = canvas.getContext('2d');
            canvas.width = width;
            canvas.height = height;
            ctx.drawImage(image, 0, 0, width, height);
            return canvas.toDataURL();
        },
        timerCallBack: function() {
            this.computeFrame();
            let self = this;
            setTimeout(function() {
                self.timerCallBack();
            }, 1000 / 30);
        },
        computeFrame: function() {
            this.canvasEl.width = this.getScreenWidth();
            this.canvasEl.height = this.getScreenHeight();

            if (this.isPortrait()) {

                let ratio = this.getScreenWidth() / this.getScreenHeight();

                this.context.drawImage(
                    this.videoEl, 
                    0, 
                    0, 
                    cameraHeight * ratio,
                    cameraHeight,
                    0, 
                    0, 
                    this.getScreenWidth(), 
                    this.getScreenHeight()
                );

            } else {

                let ratio = this.getScreenHeight() / this.getScreenWidth();

                this.context.drawImage(
                    this.videoEl, 
                    0, 
                    0, 
                    cameraWidth, 
                    cameraWidth * ratio, 
                    0, 
                    0, 
                    this.getScreenWidth(), 
                    this.getScreenHeight()
                );
            }
                
            this.context.drawImage(
                this.doctorImage, 
                0,
                0, 
                this.doctorImage.width, 
                this.doctorImage.height,
                this.imageInfo.x,
                this.imageInfo.y, 
                this.imageSize().width,
                this.imageSize().height
            );  
        },
        openImageToNewTab: function() {
            let dataUri = this.getCameraPhoto();
            let image = new Image();
            image.src = dataUri;
            image.setAttribute("style", "-webkit-user-select:none; display:block; margin:auto; padding:env(safe-area-inset-top) env(safe-area-inset-right) env(safe-area-inset-bottom) env(safe-area-inset-left); height: 100%");
            var w = window.open("");
            w.document.write(image.outerHTML);
        },
        panGesture: function(e) {
            let locationX = this.imageInfo.originX + e.deltaX;
            let locationY = this.imageInfo.originY + e.deltaY;
            let imageHalfWidth = this.imageSize().width / 2.0;
            let imageHalfHeight = this.imageSize().height / 2.0;
            let screenWidthMinusImgHalfWidth = this.getScreenWidth() - imageHalfWidth;
            let screenWidthMinusImgHalfHeight = this.getScreenHeight() - imageHalfHeight;
            if (locationX > -imageHalfWidth && 
                locationY > -imageHalfHeight &&
                locationX < screenWidthMinusImgHalfWidth &&
                locationY < screenWidthMinusImgHalfHeight) {
                this.imageInfo.x = locationX;
                this.imageInfo.y = locationY;
            }
        },
        panEndGesture: function() {
            this.imageInfo.originX = this.imageInfo.x;
            this.imageInfo.originY = this.imageInfo.y;
        },
        pinchGesture: function(e) {
            let oldSize = this.imageSize();

            let scale = (e.scale - 1.0) * 0.5;
            let newScale = this.imageInfo.originScale + scale;
            if (newScale > minImageScale && newScale < maxImageScale) {
                this.imageInfo.scale = newScale;
                let newSize = this.imageSize();
                this.imageInfo.x -= (newSize.width - oldSize.width) / 2.0;
                this.imageInfo.y -= (newSize.height - oldSize.height) / 2.0;
                this.imageInfo.originX = this.imageInfo.x;
                this.imageInfo.originY = this.imageInfo.y;
            } 
        },
        pinchEndGesture: function() {
            this.imageInfo.originScale = this.imageInfo.scale;
        },
        wheelEvent: function() {
            event.preventDefault();

            let oldSize = this.imageSize();

            let scale = this.imageInfo.scale;
            scale += event.deltaY * -0.01;
            // Restrict scale
            scale = Math.min(Math.max(minImageScale, scale), maxImageScale);
            // Apply scale transform
            this.imageInfo.scale = scale;

            let newSize = this.imageSize();
            this.imageInfo.x -= (newSize.width - oldSize.width) / 2.0;
            this.imageInfo.y -= (newSize.height - oldSize.height) / 2.0;
            this.imageInfo.originX = this.imageInfo.x;
            this.imageInfo.originY = this.imageInfo.y;
        },
        onScreenResize: function() {
            let locationX = this.imageInfo.originX;
            let locationY = this.imageInfo.originY;
            let imageHalfWidth = this.imageSize().width / 2.0;
            let imageHalfHeight = this.imageSize().height / 2.0;
            let screenWidthMinusImgHalfWidth = this.getScreenWidth() - imageHalfWidth;
            let screenWidthMinusImgHalfHeight = this.getScreenHeight() - imageHalfHeight;

            if (screenWidthMinusImgHalfWidth < locationX) {
                this.imageInfo.x = screenWidthMinusImgHalfWidth - 1;
            }
            
            if (screenWidthMinusImgHalfHeight < locationY) {
                this.imageInfo.y = screenWidthMinusImgHalfHeight - 1;
            }
        },
        hideAlertView: function() {
            let alert = document.querySelector('#alert-popover');
            alert.style.display = 'none';
        },
        copyLink: function() {
            var input = document.querySelector('input');
            input.value = document.URL;

            if (navigator.userAgent.match(/ipad|ipod|iphone/i)) {
                // for ios
            
                var oldContentEditable = input.contentEditable
                var oldReadOnly = input.readOnly;
            
                input.contentEditable = true;
                input.readOnly = false;
            
                var range = document.createRange();
                range.selectNodeContents(input);
            
                var selection = window.getSelection();
                selection.removeAllRanges();
                selection.addRange(range);
            
                input.setSelectionRange(0, 999999);
            
                input.contentEditable = oldContentEditable
                input.readOnly = oldReadOnly;

            } else {
                // for other os

                input.select();

            }

            document.execCommand('copy');

            input.blur();

            var copyButton = document.querySelector('#button-copy-link');
            copyButton.className = "btn btn-success";
            copyButton.textContent = "已複製";
        }
    }
}
</script>
<style>
#base-container {
    /* background-color: gray; */
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}
#video-view {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    /* display: none; */
}
#video-canvas {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    /* height:  100vh; */
    /* width: 100vw; */
    width: 100%;
    height: 100%;
    background-color: rgba(102, 218, 189, 0.493);
}
#btn-group-container {
    position: fixed;
    bottom: 5%;
    width: 100vw;
}
.btn-style {

    background: none;
    border: none;
}
#btn-shot {
    font-size: 1.5em;
}
#btn-change {
    font-size: 1em;
}
.btn-group {
    max-width: 120px;
}
#alert-popover {
    background-color: rgb(236, 215, 191);
    position: fixed;
    border-radius: 20px;
    /* display: none; */
    left: 0;
    right: 0;
    width: 70%;
    margin: auto;
    padding: 20px;
    top: 50%;
    z-index: 10;
    transform: translateY(-50%);
}
